@import "../../assets/styles/fonts.css";
@import "../../assets/styles/colors.css";

.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;
}

.footer__copyright {
    font-family: "Manrope-Light";
    font-size: 20px;
    line-height: 20px;

    color: var(--c-white);
}

.footerArrow {
    display: block;
    height: 56px;
    width: 36px;
    padding-top: 8px;
    transition: 0.5s;
}

.footerArrow:hover {
    padding-top: 0;
}

@media screen and (max-width: 1161px) {
    .footer {
        margin-bottom: 27px;
    }

    .footer__copyright {
        font-size: 14px;
        line-height: 20px;
    }

    .footerArrow {
        display: block;
        height: 36px;
        width: 24px;
        padding-top: 4px;
        transition: 0.5s;
    }

    .footerArrow:hover {
        padding-top: 0;
    }
}